import React from 'react';
import { Collapse, Timeline, Icon } from 'antd';

import './courseBlock.sass';

const { Panel } = Collapse;

class Course extends React.Component{
  state = {
    show: false
  }

  onChange = (val) => {
    this.setState({
      show: val.length !== 0
    })
  }

  render () {
    const { title, detail, contentList } = this.props

    return (
      <div className="course">
        <Collapse onChange={this.onChange}>
          <Panel showArrow={false} header={
            <div className="course-pannel">
                <div className="course-pannel-title">
                  { title }
                </div>
                <div className="course-detail-wrap">
                  <h3 className="course-detail-title">
                    { detail.title }
                  </h3>
                  <div className="course-detail-content">
                    { detail.content }
                  </div>
                  <div className="course-icon-wrap">
                    <Icon type={ this.state.show ? "up" : "down"} />
                  </div>
                </div>
            </div>
          }>
            <Timeline>
              { contentList.map((item, index) => 
                <Timeline.Item key={index}>
                  { item }
                </Timeline.Item>)
              }
            </Timeline>
          </Panel>
        </Collapse>
      </div>
    )
  }
}

export default Course;
