import React from 'react';
import { connect } from 'react-redux';
import { changeCouse } from '../store/actions';

import Title from '../components/title';
import AvaList from '../components/avaList';
import CourseBlock from '../components/courseBlock';
import QRCode from '../components/qrCode';

import courseStyle from './course.module.scss'

import { Card, Col, Row } from 'antd';

import { Carousel } from 'element-react/next';

import 'element-theme-default';

const { Meta } = Card;

@connect(
  state => ({...state.course})
)
class Course extends React.Component{
  constructor (props) {
    super();
    this.getContent(props);
  }

  _renderTitleBlock = (background, title, subTitle, price, des) =>
    <div className={ courseStyle.titleBlock } style={ { backgroundImage: `url(${ process.env.PUBLIC_URL + background})` } }>
      <div className={ courseStyle.titleWrap }>
      <Row gutter={100}>
        <Col span={16}>
          <h1>{ title }</h1>
          <h3>{ subTitle }</h3>
        </Col>
        <Col span={8}>
          <h1>{ price }</h1>
          {
            des.map((item, index) =>
              <p key={ index }>{ item }</p>
          )
          }
        </Col>
      </Row>
        {/* <h1>{ title }</h1>
        <h2>{ subTitle }</h2> */}
      </div>
    </div>

  _renderTextContent = (contentList) =>
    <div className={ courseStyle.courseTextContent }>
      { contentList.map((item, index) => <p key={index}>{ item }</p>) }
    </div>

  _renderBlock = (title, content) =>
    <div className={ courseStyle.courseBlock }>
      <Title text={ title } />
      { content }
    </div>

  _renderIntrodution = (title, content) =>
    <div>
      { this._renderBlock(title,
          this._renderTextContent(content)
      ) }
    </div>

  _renderSuit = (title, stuList) =>
    <div>
      {
        this._renderBlock(title,
          <AvaList data={ stuList } grid={ { gutter: 16, column: 4 } }/>
        )
      }
    </div>

  _renderCourse = (title, courseList) =>
      <div>
        {
          this._renderBlock(title,
            courseList.map((item, index) =>
              <CourseBlock
              title={ item.title }
              detail={ item.detail }
              contentList={ item.contentList }
              key={ index }
              />
            )
          )
        }
      </div>

  _renderPrice = (title, price, des) =>
      <div>
        {
          this._renderBlock(title,
            <Row gutter={16}>
              <Col span={4} offset={10}>
                <Card
                  hoverable
                  cover={<div className={ courseStyle.priceBlock }>{ price }</div>}
                >
                  <Meta description={
                      des.map((item, index) =>
                        <p key={ index }>{ item }</p>
                      )
                    } />
                </Card>
              </Col>
            </Row>
          )
        }
      </div>

  _renderCourseScreen = (title, imgList) =>
    <div>
      {
        this._renderBlock(title,
          <Carousel interval="4000" type="card">
            {
              imgList.map((item, index) =>
                <Carousel.Item key={index}>
                  <img alt="课程截图" src={item} width="100%" height="100%"/>
                </Carousel.Item>
              )
            }
          </Carousel>
        )
      }
    </div>

  getContent (props) {
    const { dispatch, match: { params: { id } } } = props;
    dispatch(changeCouse(id));
  }

  componentWillReceiveProps (nextProps) {
    if (nextProps.location.pathname !== this.props.location.pathname) {
      this.getContent(nextProps);
    }
  }

  render () {
    const { content: { course } } = this.props;

    if (!course) return <div></div>

    const base = course.base;
    const intro = course.introduction;
    const suit = course.suit;
    const courseInfo = course.courseInfo;
    // const price = course.price;
    const courseScreen = course.courseScreen;

    return (
      <div className={ courseStyle.course }>
        { this._renderTitleBlock(base.background, base.title, base.subTitle, base.price, base.des) }
        { this._renderIntrodution(intro.title, intro.content) }
        { this._renderSuit(suit.title, suit.studentList) }
        { this._renderCourse(courseInfo.title, courseInfo.courseList) }
        {/* { this._renderPrice(price.title, price.price, price.des) } */}
        { this._renderCourseScreen(courseScreen.title, courseScreen.imgList) }
        <QRCode />
      </div>
    )
  }
}

export default Course;
